import React, { Component } from 'react';
import Button from 'material-ui/Button';
import { Link } from 'react-router';
import TextField from 'material-ui/TextField';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import { blue, grey } from 'material-ui/colors';
import { connect } from 'react-redux'
import { postSignup } from '../modules/signup';

const styles = {
  loginContainer: {
    textAlign: 'center',
    textHeight: '200px',
    backgroundColor: '#fff',
    padding: '2em 0 2em 0',
    borderRadius: '5px',
    border: '1px solid rgba(0,0,0,0.1)',
    marginTop: '10%',
    boxShadow: '2px 2px 20px #ccc',
  },
  textField: {
    width: '40%',
    minWidth: '200px',
  },
  textColor: {
    color: grey[800],
    padding: '20px',
    fontSize: '0.9em',
    margin: '20px',
  },
  link: {
    color: blue[800],
    textDecoration: 'none',
    '&:hover': {
      color: blue[600],
    },
  },
};

class Signup extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      confirmPassword: '',
      msg: '',
      error: false,
    };
  }

  componentWillUnmount() {
    console.warn('SIGNUP unmounts.\nRemember to clear message.');
  }

  // 双箭头，高级用法（待研究）
  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
    if (name === 'confirmPassword') {
      if (event.target.value === this.state.password) {
        this.setState({
          error: false,
        });
      }
    }
  }

  onSubmit = () => {
    if (this.state.confirmPassword !== this.state.password) {
      this.setState({
        error: true,
      });
      return;
    } else {
      this.props && this.props.postSignup(this.state.email, this.state.password, this.state.confirmPassword);
    }
  }

  render() {
    return (
      <div className={this.props.classes.loginContainer}>
        <div>
          <TextField className={this.props.classes.textField}
            id="email"
            label="E-mail"
            margin="normal"
            autoFocus
            required
            placeholder='Enter the E-mail address'
            onChange={this.handleChange('email')}
          />
        </div>
        <div>
          <TextField className={this.props.classes.textField}
            id="password"
            label="Password"
            type="password"
            margin="normal"
            placeholder='Set password here, at least 4 characters long'            
            required
            onChange={this.handleChange('password')}
          />
        </div>
        <div>
          <TextField className={this.props.classes.textField}
            error={this.state.error}
            id="confirmpassword"
            label="Confirm password"
            type="password"
            margin="normal"
            placeholder='Confirm your password'            
            required
            onChange={this.handleChange('confirmPassword')}
            helperText={this.state.error?'Passwords are not same':null}
          />
        </div>
        <div className={this.props.classes.textColor}>
          Already have an account?
          <Link to="/login" className={this.props.classes.link}> Login here </Link>
        </div>
        <div>
          <Button
            raised
            color="accent"
            className={this.props.classes.button}
            onClick={this.onSubmit}
            disabled={this.state.error}
          >
             Sign Up
          </Button>
        </div>
      </div>
    );
  }
}


const mapDispatchToProps = {
  postSignup,
}

const mapStateToProps = (state) => ({
  userProfile : state.signup,
})

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(createStyleSheet(styles))(Signup));



