import axios from 'axios';
import { devServerAddress } from '../../../global/const';

// ------------------------------------
// Constants
// ------------------------------------
export const POST_SIGNUP_START = 'POST_SIGNUP_START';
export const POST_SIGNUP_DONE = 'POST_SIGNUP_DONE';

// ------------------------------------
// Actions
// ------------------------------------
export function postSignupStart() {
  return {
    type: POST_SIGNUP_START,
    message: 'Start new user signing up...',
  };
}

export function postSignupDone(users, msg) {
  return {
    type: POST_SIGNUP_DONE,
    userProfile: users,
    message: msg,
  };
}

export function postSignup(email, password, confirmPassword) {
  return (dispatch, getState) => {
    dispatch(postSignupStart());
    axios.post(
      `${devServerAddress}/signup`,
      {
        email,
        password,
        confirmPassword,
      })
      .then((response) => {
        console.log('返回数据', response);
        if (response.data.status === 'success') {
          return dispatch(postSignupDone(response.data, response.data.msg));
        } else if (response.data.status === 'failure') {
          return dispatch(postSignupDone(null, response.data.msg));          
        }
      })
      .catch((error) => {
        return dispatch(postSignupDone(null, 'Network error.'));
      });
  };
}

// ------------------------------------
// Action Handlers
// ------------------------------------

const USERS_ACTION_HANDLERS = {
  [POST_SIGNUP_START]: (state, action) => {
    return ({ ...state, message: action.message });
  },
  [POST_SIGNUP_DONE]: (state, action) => {
    return ({ ...state, userProfile: action.userProfile, message: action.message });
  },
};

// ------------------------------------
// Reducer
// ------------------------------------
const initialState = {
  userProfile: null,
  message: '',
};

export default function createUserReducer(state = initialState, action) {
  const handler = USERS_ACTION_HANDLERS[action.type];
  return handler ? handler(state, action) : state;
}



